
import header from '@/components/header'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import { MessageBox, Indicator, Toast } from 'mint-ui'
import API from '@/api/vacation'
import { setCorrectSQDate, compareDate } from '@/utils/utils'

export default {
components: { 'v-header': header, Group, Datetime, PopupPicker },
created () {
Indicator.open({
text: '...',
spinnerType: 'fading-circle'
})
API.GET_VACATION_TYPE(this, {}).then((rep) => {
Indicator.close()
let result = rep.body
if (result.success) {
this.list = result.data.map((item) => {
item.name = item.aaa103
item.value = item.aaa102
delete item.aaa103
delete item.aaa102
return item
})
this.vacationTypeList.push(this.list)
} else {
MessageBox.alert(result.message, '')
}
}, (rep) => {
Indicator.close()
Toast({
message: '',
position: 'middle',
duration: 1000
})
})
},
data () {
return {
headtitle: '',
isTypeOne: true,
formData: {
token: this.$store.getters.getUser.token,
zhgl_qj01001: this.$store.getters.getUser.data.name, // 
zhgl_qj01003: '', // 
zhgl_qj01002: this.$store.getters.getUser.data.orgname, // 
zhgl_qj01004: '', // 
zhgl_qj01005: '', // 
zhgl_qj01006: '', // 
zhgl_qj01007: '', // 
zhgl_qj01009: '', // 
zhgl_qj01016: '', // 
zhgl_qj01027: '01', // 01     02 ()
zhgl_qj01028: '', // 
zhgl_qj01030: '' 
},
vacationTypeList: [], // 
vacationvalue: [],
showName: true, // name
list: [] 
}
},
methods: {
setStartDate: function (dateTime) {
this.formData.zhgl_qj01006 = ''
this.formData.zhgl_qj01007 = ''
Indicator.open({
text: '...',
spinnerType: 'fading-circle'
})
let time = dateTime.substring(0, 10)
API.IS_WORK_DAY(this, { time }).then((rep) => {
let result = rep.body
Indicator.close()
if (result.success) {
if (result.data === 1) {
this.formData.zhgl_qj01006 = setCorrectSQDate(dateTime, 'begin')
if (this.formData.zhgl_qj01006 && this.formData.zhgl_qj01007) {
this.setHours()
}
} else {
MessageBox.alert('', '')
}
} else {
MessageBox.alert(result.message, '')
}
}, (rep) => {
Indicator.close()
Toast({
message: '',
position: 'middle',
duration: 1000
})
})
},
setEndDate: function (dateTime) {
if (!compareDate(dateTime, this.formData.zhgl_qj01006)) {
this.formData.zhgl_qj01007 = ''
MessageBox.alert('', '')
return false
}
Indicator.open({
text: '...',
spinnerType: 'fading-circle'
})
let time = dateTime.substring(0, 10)
API.IS_WORK_DAY(this, { time }).then((rep) => {
let result = rep.body
Indicator.close()
if (result.success) {
if (result.data === 1) {
this.formData.zhgl_qj01007 = setCorrectSQDate(dateTime, 'end')
this.setHours()
} else {
MessageBox.alert('', '')
}
} else {
MessageBox.alert(result.message, '')
}
}, (rep) => {
Indicator.close()
Toast({
message: '',
position: 'middle',
duration: 1000
})
})
},
setHours: function () {
Indicator.open({
text: '...',
spinnerType: 'fading-circle'
})
API.WINDOW_GET_HOURS(this, { zhgl_qj01006: this.formData.zhgl_qj01006, zhgl_qj01007: this.formData.zhgl_qj01007 }).then((rep) => {
let result = rep.body
Indicator.close()
if (result.success) {
this.formData.zhgl_qj01008 = this.formData.zhgl_qj01030 = result.data + ''
} else {
MessageBox.alert(result.message, '')
}
}, (rep) => {
Indicator.close()
Toast({
message: '',
position: 'middle',
duration: 1000
})
})
},
reset: function () {
this.formData = {
zhgl_qj01001: this.$store.getters.getUser.data.name, // 
zhgl_qj01003: '', // 
zhgl_qj01002: this.$store.getters.getUser.data.orgname, // 
zhgl_qj01004: '', // 
zhgl_qj01005: '', // 
zhgl_qj01006: '', // 
zhgl_qj01007: '', // 
zhgl_qj01009: '', // 
zhgl_qj01016: '', // 
zhgl_qj01027: '01', // 01     02 ()
zhgl_qj01028: '', // 
zhgl_qj01030: '' 
}
},
confirm: function () {
this.formData.zhgl_qj01027 = '01'
this.formData.zhgl_qj01028 = '0'
this.formData.zhgl_qj01004 = this.vacationvalue[0]
if (this.validateForm()) {
Indicator.open({
text: '...',
spinnerType: 'fading-circle'
})
this.formData.zhgl_qj01008 = this.formData.zhgl_qj01008.substring(0, this.formData.zhgl_qj01008.length - 2)
this.formData.zhgl_qj01030 = this.formData.zhgl_qj01030.substring(0, this.formData.zhgl_qj01030.length - 2)
this.windowConfirm()
} else {
MessageBox.alert('', '')
}
},
validateForm: function () {
let isPass = true
for (let key in this.formData) {
if (this.formData[key] === '') {
isPass = false
}
}

if (!this.isMobileNo(this.formData.zhgl_qj01009)) {
isPass = false
}

return isPass
},
isMobileNo: function (value) {
var reg = /^[1][3,4,5,7,8,9][0-9]{9}$/
if (reg.test(value)) {
return true
}
return false
},
windowConfirm: function () {
API.WINDOW_APPLY_VACATION(this, this.formData).then((rep) => {
let result = rep.body
Indicator.close()
if (result.success) {
if (result.state) {
MessageBox.alert('2s', '')
setTimeout(() => {
this.$router.go(-1)
}, 2000)
} else {
MessageBox.alert('' + result.message, '')
}
} else {
MessageBox.alert(result.message, '')
}
}, (rep) => {
Indicator.close()
Toast({
message: '',
position: 'middle',
duration: 1000
})
})
}
}
}




